  import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
  import cmdPageBody from "@/components/cmd-page-body/cmd-page-body.vue"
  import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
  import cmdInput from "@/components/cmd-input/cmd-input.vue"

  export default {
    components: {
      cmdNavBar,
      cmdPageBody,
      cmdTransition,
      cmdInput
    },

    data() {
      return {
        query: {
          oldPwd: '',
		  newPwd: '',
		  againPwd: '',
        },
        phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
      };
    },

    methods: {
	  // 提交信息
	  async commitData(){
		  if (this.validData()){
			  let ret = await this.$http({
				  url: '/changePwd',
				  method: 'POST',
				  data: this.query
			  })
			  if (ret.code == 0){
				  uni.showToast({
					  title: ret.message + ",请重新登录"
				  })
				  setTimeout(function() {
					  uni.navigateTo({
					  	url: '/pages/login/login'
					  })
				  }, 1000);
				  
			  }else{
				  uni.showToast({
				  	icon: 'none',
					title: ret.message
				  })
			  }
		  }
	  },
	  // 验证表单
	  validData(){
		if (this.query.oldPwd == ''){
			uni.showToast({
				icon: 'none',
				title: '旧密码不能为空'
			})
			return false;
		}
		if (this.query.newPwd == ''){
			uni.showToast({
				icon: 'none',
				title: '新密码不能为空'
			})
			return false;
		}
		if (this.query.againPwd != this.query.newPwd){
			uni.showToast({
				icon: 'none',
				title: '两次密码不一致'
			})
			return false;
		}
		return true;
	  },
      /**
       * 获取验证码
       */
      fnGetPhoneCode() {
        if (this.phoneReg.test(this.mobile.phone)) {
          uni.showToast({
            title: "正在发送验证码",
            icon: "loading",
            success: () => {
              // 成功后显示倒计时60s后可在点击
              this.safety.state = true;
              // 倒计时
              this.safety.interval = setInterval(() => {
                if (this.safety.time-- <= 0) {
                  this.safety.time = 60;
                  this.safety.state = false;
                  clearInterval(this.safety.interval);
                }
              }, 1000);
              uni.showToast({
                title: "发送成功",
                icon: "success"
              })
            }
          })
        } else {
          uni.showToast({
            title: "手机号不正确",
            icon: "none"
          })
        }
      }
    },

    beforeDestroy() {
      /**
       * 关闭页面清除轮询器
       */
      clearInterval(this.safety.interval);
    }
  }
